import { useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import { db, auth } from '../firebaseConfig'; // Firebase config
import { doc, getDoc, updateDoc, setDoc } from 'firebase/firestore'; // Se restauran updateDoc y setDoc
import { onAuthStateChanged, signOut } from 'firebase/auth'; // Importamos Firebase Auth
import Calendar from 'react-calendar'; // Calendario interactivo
import 'react-calendar/dist/Calendar.css'; // Estilos del calendario

const ProfessionalPage = () => {
  const { subdomain } = useParams();
  const [professional, setProfessional] = useState(null);
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [selectedSlot, setSelectedSlot] = useState(null);
  const [turnosVigentes, setTurnosVigentes] = useState([]);
  const [showConfirmation, setShowConfirmation] = useState(false);
  const [user, setUser] = useState(null); // Para almacenar el usuario logueado
  const [errorMessage, setErrorMessage] = useState(""); // Para mostrar el mensaje de error

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      if (currentUser) {
        setUser(currentUser);
        setErrorMessage("");
      } else {
        setUser(null);
        setErrorMessage("Debe estar logueado para reservar un turno.");
      }
    });

    return () => unsubscribe();
  }, []);

  useEffect(() => {
    const fetchProfessional = async () => {
      const docRef = doc(db, "professionals", subdomain);
      const docSnap = await getDoc(docRef);

      if (docSnap.exists()) {
        setProfessional(docSnap.data());
      } else {
        console.log("No se encontr ningn profesional con ese subdominio.");
      }
    };

    fetchProfessional();
  }, [subdomain]);

  const handleDateChange = async (date) => {
    setSelectedDate(date);

    const formattedDate = `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
    const docRef = doc(db, "turnos", formattedDate);
    const docSnap = await getDoc(docRef);

    if (docSnap.exists()) {
      setTurnosVigentes(docSnap.data().bookedAppointments || []);
    } else {
      setTurnosVigentes([]);
    }
  };

  const handleAppointmentClick = (slot) => {
    if (!user) {
      setErrorMessage("Debe estar logueado para reservar un turno.");
      return;
    }

    setSelectedSlot(slot);
    setShowConfirmation(true);
  };

  const confirmTurno = async () => {
    if (!user) {
      setErrorMessage("Debe estar logueado para reservar un turno.");
      return;
    }

    const formattedDate = `${selectedDate.getFullYear()}-${selectedDate.getMonth() + 1}-${selectedDate.getDate()}`;
    const docRef = doc(db, "turnos", formattedDate);
    const docSnap = await getDoc(docRef);

    if (docSnap.exists()) {
      await updateDoc(docRef, {
        availableSlots: professional.availableSlots.filter((s) => s !== selectedSlot),
        bookedAppointments: [
          ...turnosVigentes,
          { time: selectedSlot, patient: user.displayName, dni: '12345678', telefono: '123456789', obraSocial: 'Obra Social' },
        ],
      });
    } else {
      await setDoc(docRef, {
        availableSlots: professional.availableSlots.filter((s) => s !== selectedSlot),
        bookedAppointments: [
          { time: selectedSlot, patient: user.displayName, dni: '12345678', telefono: '123456789', obraSocial: 'Obra Social' },
        ],
      });
    }

    alert(`Turno reservado: ${selectedSlot}`);
    setShowConfirmation(false);
  };

  const cancelTurno = async (turno) => {
    const formattedDate = `${selectedDate.getFullYear()}-${selectedDate.getMonth() + 1}-${selectedDate.getDate()}`;
    const docRef = doc(db, "turnos", formattedDate);

    const docSnap = await getDoc(docRef);
    if (docSnap.exists()) {
      const updatedBookedAppointments = turnosVigentes.filter((t) => t.time !== turno.time);
      const updatedAvailableSlots = [...professional.availableSlots, turno.time];

      await updateDoc(docRef, {
        availableSlots: updatedAvailableSlots,
        bookedAppointments: updatedBookedAppointments,
      });

      alert(`Turno cancelado: ${turno.time}`);
    }
  };

  const handleLogout = () => {
    signOut(auth);
  };

  return (
    <div className="flex min-h-screen bg-gray-100">
      {/* Men a la izquierda que fue reemplazado por el calendario */}
      <div className="w-1/4 p-4 bg-slate-800 shadow-lg">
        <h2 className="text-xl mb-4 text-white">Calendario</h2>
        <Calendar
          onChange={handleDateChange}
          value={selectedDate}
          tileClassName={({ date, view }) => {
            if (view === 'month' && professional?.availableSlots?.some(slot => slot.date === date.toLocaleDateString())) {
              return 'react-calendar__tile--has-turn'; // Aplica la clase personalizada
            }

            return '';
          }}
          className="react-calendar"  // Esta es la clase principal que se aplica al calendario
        />

      </div>

      {/* Contenido a la derecha */}
      <div className="w-3/4 p-6 space-y-6">
        {professional && (
          <div className="flex justify-between items-center">
            {/* Nombre del profesional */}
            <div>
              <p className="text-gray-800 text-lg font-semibold">{professional.name}</p>
              <p>{professional.specialty}</p>
            </div>

            {/* Men horizontal y Cerrar sesin */}
            <div className="flex items-center space-x-6">
              <nav className="flex space-x-6">
                <a href="#" className="text-gray-800 hover:underline">Agenda</a>
                <a href="#" className="text-gray-800 hover:underline">Clientes</a>
                <a href="#" className="text-gray-800 hover:underline">Configuracin</a>
              </nav>
              <button
                onClick={handleLogout}
                className="bg-red-500 text-white py-1 px-4 rounded hover:bg-red-600"
              >
                Cerrar Sesin
              </button>
            </div>
          </div>
        )}

        {/* Mostrar mensaje de error si existe */}
        {errorMessage && (
          <div className="bg-red-200 text-red-700 p-2 rounded">
            {errorMessage}
          </div>
        )}

        {/* Turnos del da */}
        <div className="bg-white p-4 shadow-lg rounded-lg">
          <h2 className="text-xl mb-4">
            Turnos del da: {selectedDate.toLocaleDateString('es-ES', { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' })}
          </h2>
          <ul>
            {professional?.availableSlots?.map((slot, index) => {
              const turnoOcupado = turnosVigentes.find(t => t.time === slot);
              return (
                <li key={index} className="flex justify-between items-center mb-2">
                  <span>{slot}</span>
                  {turnoOcupado ? (
                    <button onClick={() => cancelTurno(turnoOcupado)} className="bg-red-500 text-white py-1 px-4 rounded hover:bg-red-600">Cancelar Turno</button>
                  ) : (
                    <button onClick={() => handleAppointmentClick(slot)} className="bg-green-500 text-white py-1 px-4 rounded hover:bg-green-600">Asignar Turno</button>
                  )}
                </li>
              );
            })}
          </ul>
        </div>


        {/* Confirmacin de turno */}
        {selectedSlot && showConfirmation && (
          <div className="p-4 border rounded bg-gray-200">
            <p className="text-lg">Ests seguro de reservar el turno para: <strong>{selectedSlot}</strong>?</p>
            <div className="flex space-x-4 mt-2">
              <button onClick={confirmTurno} className="bg-green-500 text-white py-1 px-4 rounded hover:bg-green-600">S</button>
              <button onClick={() => setShowConfirmation(false)} className="bg-red-500 text-white py-1 px-4 rounded hover:bg-red-600">No</button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default ProfessionalPage;
